<template>
  <div id="loading-container" class="vs-con-loading__container">
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>
    <component :is="`Beneficiary${dataLayout}`" :key="Date.now()" :parent="this"/>
  </div>
</template>

<script>
import BeneficiaryListLayout from '@/views/beneficiary/beneficiary-root/beneficiary-browse/BeneficiaryListLayout'
import BeneficiaryCardLayout from '@/views/beneficiary/beneficiary-root/beneficiary-browse/BeneficiaryCardLayout'
import BeneficiaryPivotLayout from '@/views/beneficiary/beneficiary-root/beneficiary-browse/BeneficiaryPivotLayout'
import AlertMixin from '@/mixins/commons/AlertMixin'
import BeneficiaryTableMixin from '@/mixins/beneficiary/BeneficiaryTableMixin'

export default {
  name: 'beneficiary-browse',
  mixins: [BeneficiaryTableMixin, AlertMixin],
  components: {
    BeneficiaryListLayout,
    BeneficiaryCardLayout,
    BeneficiaryPivotLayout
  },
  data: function () {
    return {
      dataLayout: 'ListLayout'
    }
  },
  mounted: function () {
    this.loadBeneficiaryTableData()
  },
  created: function () {
    this.$eventHub.$on('toolClicked', (toolId) => { this.dataLayout = toolId })
  },
  beforeDestroy: function () {
    this.$eventHub.$off('toolClicked', (toolId) => { this.dataLayout = toolId })
  }
}
</script>
