import BeneficiaryService from '@/services/beneficiary/BeneficiaryService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let BeneficiaryTableMixin = {
  mixins: [ListLayoutMixin, LoadingMixin],
  props: {
    defaultBeneficiaryTableFilter: {
      type: String,
      default: null
    },
    defaultBeneficiaryTableOrderBy: {
      type: String,
      default: null
    },
    typeBehaviourEnumId: {
      type: String,
      default: null
    },
    statusId: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      beneficiaryTableData: [],
      beneficiaryTableFilterTimer: null,
      beneficiaryTableFilter: this.defaultBeneficiaryTableFilter,
      beneficiaryTableOrderBy: this.defaultBeneficiaryTableOrderBy,
      beneficiaryTableContainer: 'loading-container',
      beneficiaryTableBusy: false,
      beneficiaryTableTotalRows: 0,
      beneficiaryTableCurrentPage: 1,
      beneficiaryTablePerPage: 10,
      columnDefs: [{
        headerName: this.$t('ID'),
        field: 'beneficiaryId'
      }, {
        headerName: this.$t('BENEFICIARY_NAME'),
        field: 'beneficiaryName'
      }, {
        headerName: this.$t('ACCOUNT_NUMBER'),
        field: 'accountNumber',
        format: 'Number'
      }, {
        headerName: this.$t('IBAN'),
        field: 'iban'
      }, {
        headerName: this.$t('BENEFICIARY_TYPE'),
        field: 'typeName'
      }, {
        headerName: this.$t('STATUS'),
        field: 'statusDescription'
      }]
    }
  },
  computed: {
    beneficiaryTableNoDataText: function () {
      return this.beneficiaryTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    beneficiaryTableTotalPages: function () {
      return Math.ceil(this.beneficiaryTableTotalRows / this.beneficiaryTablePerPage)
    },
    beneficiaryTablePagingInfo: function () {
      return this.pagingInfo(this.beneficiaryTableCurrentPage, this.beneficiaryTablePerPage, this.beneficiaryTableTotalRows)
    }
  },
  methods: {
    loadBeneficiaryTableData (loadingContainerId) {
      let self = this
      this.beneficiaryTableBusy = true
      this.beneficiaryTableContainer = loadingContainerId || this.beneficiaryTableContainer
      this.startLoading(this.beneficiaryTableContainer)
      BeneficiaryService.beneficiaries({
        pageIndex: this.beneficiaryTableCurrentPage - 1,
        pageSize: this.beneficiaryTablePerPage,
        filter: this.beneficiaryTableFilter,
        orderByField: this.beneficiaryTableOrderBy,
        typeBehaviourEnumId: this.typeBehaviourEnumId,
        statusId: this.statusId
      }).then(function (response) {
        self.stopLoading(self.beneficiaryTableContainer)
        self.beneficiaryTableBusy = false
        self.beneficiaryTableTotalRows = response.data.totalRows
        self.beneficiaryTableData = response.data.beneficiaryList || []
      }).catch(function (error) {
        self.stopLoading(self.beneficiaryTableContainer)
        self.beneficiaryTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshBeneficiaryTable () {
      this.loadBeneficiaryTableData()
    },
    filterBeneficiaryTable (filter) {
      let self = this
      if (this.beneficiaryTableFilter !== filter) {
        clearTimeout(this.beneficiaryTableFilterTimer)
        this.beneficiaryTableFilterTimer = setTimeout(() => {
          self.beneficiaryTableFilter = filter
          self.loadBeneficiaryTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.beneficiaryTableFilterTimer)
      }
    },
    orderBeneficiaryTable (orderBy, active) {
      this.beneficiaryTableOrderBy = (active ? '' : '-') + orderBy
      this.loadBeneficiaryTableData()
    },
    beneficiaryName: function (item) {
      return item.nickname ? item.nickname : item.beneficiaryName
    },
    beneficiaryAccountNumber: function (item) {
      return item.iban ? item.iban : item.accountNumber
    }
  },
  watch: {
    beneficiaryTableCurrentPage: function () {
      this.refreshBeneficiaryTable()
    },
    beneficiaryTablePerPage: function () {
      this.refreshBeneficiaryTable()
    }
  }
}

export default BeneficiaryTableMixin
